<template>
  <div id="delete-admin__modal">
    <div v-if="!deleteAdminFlag" class="delete-admin__inner">
      <div>
        <p>회원 탈퇴를 하면 서비스를</p>
        <p>더이상 사용할 수 없습니다</p>
        <p>정말 탈퇴하시겠습니까?</p>
      </div>
      <div class="button__wraps">
        <button @click="deleteAdminConfirm">회원탈퇴</button>
        <button class="cancel" @click="cancel">취소하기</button>
      </div>
    </div>
    <div v-else-if="deleteAdminFlag === 'inputPw'" class="delete-admin__inner">
      <div>
        <p style="transform: translateY(-16px);" class="mb-2">
          비밀번호를 입력하여 주십시오
        </p>
        <v-text-field
          outlined
          type="password"
          class="search_input"
          v-model="secessionPw"
          :rules="[
            v =>
              (v || '').length > 0 ||
              `탈퇴하시려면 비밀번호를 입력하여 주십시오`
          ]"
          color="main"
          placeholder="비밀번호"
        />
      </div>
      <div class="button__wraps">
        <button @click="deleteAdmin">탈퇴</button>
        <button class="cancel" @click="cancel">취소하기</button>
      </div>
    </div>
    <div v-else class="delete-admin__inner">
      <div>
        <p>회원탈퇴가 완료되었습니다</p>
        <p>자동으로 로그아웃 됩니다</p>
      </div>
      <div class="button__wraps">
        <button @click="logout">로그아웃</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      deleteAdminFlag: false,
      secessionPw: ""
    };
  },
  computed: {
    ...mapGetters(["memberInfo"])
  },
  methods: {
    cancel() {
      this.$emit("cancel");
    },

    deleteAdminConfirm() {
      this.deleteAdminFlag = "inputPw";
    },
    deleteAdmin() {
      if (this.secessionPw === "") {
        return;
      }

      const parameter = {
        member: {
          id: this.memberInfo.id,
          memberPw: this.secessionPw
        }
      };
      this.$store.dispatch("REQUEST_SECESSION_MEMBER", parameter);

      this.secessionPw = "";
    },

    logout() {
      this.$store.dispatch("REQUEST_LOGOUT");
      this.$store.commit("END_DEEMED");
    }
  }
};
</script>

