<script>
import { mapGetters } from "vuex";
// import MainNotice from "@/components/main/MainNotice"
// import Footer from "@/components/common/Footer"
// import Swiper from "@/components/common/Swiper"

import DeleteAdminModal from "@/components/common/DeleteAdminModal.vue";

import myMixin from "@/mixin/mixin";

import MobileHeader from "@/components/common/MobileHeader.vue";
import TermPopup2 from "@/components/common/TermPopup2.vue";

// import TermPopup2 from "@/components/common/TermPopup2"
export default {
  name: "MainPage",
  components: {
    DeleteAdminModal,
    MobileHeader,
    TermPopup2
  },
  mixins: [myMixin],
  layout: "admin",
  props: {},

  data() {
    return {
      corp: null,
      deleteAdminFlag: false,
      secessionPw: "",
      displayAddFlag: false,
      password: "",
      agreeFlag: false,
      corpSearchTxt: ""
    };
  },
  computed: {
    ...mapGetters(["memberInfo"])
  },

  mounted() {},
  methods: {
    backRouting() {
      this.$router.go(-1);
    },

    changeProfile() {
      alert("서비스 준비 중입니다");
    },

    adAgreeBtnClick() {
      this.agreeFlag = true;
      this.$store.commit("START_DEEMED");
    },

    adAgreeBtnClickSubmit(val) {
      this.agreeFlag = false;
      this.$store.commit("END_DEEMED");

      const parameter = {
        member: {
          id: this.memberInfo.id,
          memberMarketingAgree: val
        }
      };
      this.$store.dispatch("REQUEST_CHANGE_MARKET_AGREE_MEMBER", parameter);
    },

    openDeleteAdminModal() {
      this.deleteAdminFlag = true;
      this.$store.commit("START_DEEMED");
    },

    closeModal() {
      this.deleteAdminFlag = false;
      this.$store.commit("END_DEEMED");
    },

    move(path) {
      this.$router.push(path);
    },
    logout() {
      this.$store.dispatch("REQUEST_LOGOUT");
    }
  }
};
</script>

